@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

$roboto-slab : Helvetica Neue, Helvetica, Arial, sans-serif;
$lobster : 'Lobster', cursive;

$bgColor : #216353;
$navColor : rgb(27, 27, 27);
$contentColor : #ff1e566e; 
$cardColor : #e7ebeb;
$cardColorHover : #ccd3d3;

@mixin bacgroundImage($path, $position:0 0, $repeat:no-repeat, $size:cover){
    background: {
        image : url($path);
        position: $position;
        repeat : $repeat;
        size : $size
    };
}

body {
    margin : 0;
    padding: 0;
    overflow: hidden;
    font-family: $roboto-slab;
}



lottie-player {

    position: fixed;
    z-index: 9999999;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%)
    

}

.msg {
    margin: 5px;
    width:95%;
    border: 1px solid;
    padding:10px;
    opacity: 80%;
    color: grey;
  }

  
  .msg-error {
    border-color: #d32f2f;
    background-color: #ef5350;
    color: white;
  }

.login-container, .register-container {
    //  top: 50%;
    // left: 50%;
    // margin-right:-50%;
    // transform: translate(-50%, -50%);
    margin-top: 5%;
    margin-bottom: 5%;
    border-radius: 4px;
}
.login-form, .register-form {
  
    margin: auto;
    top: 50px;
    display: flex;
    flex-direction:row;
    justify-content: flex-start;
    width: 600px;
    height: 450px;
    border-radius: 2px;
  
    box-shadow: 5px 0px 10px 0px rgb(177, 177, 177);
}



h2 {
    color: $bgColor;
    font-size: 24px;
}

.left {
   margin: 10px;
    
}

.right {
    background-color:$bgColor;
    width: 200px;
    flex: 1;
    
}

.right h1 {
    color: white;
    font-family: $lobster;
    text-align: center;
    font-size: 64px;
    margin-top: 40%;
    opacity: 90%;
}

.left form input[type=text]:focus {
    border-bottom: 1px solid $bgColor;
    box-shadow: 0 1px 0 0 #000;
  }

p {
    font-size: 12px;
   color: grey;
}


//dashboard
.container {
    margin: 0;
    padding: 0;
    height: 100vh;
    width: 100vw;
    display:flex;
    flex-direction:column;
    background-color:$bgColor;
    // @include bacgroundImage('/img/bg.jpg');
}

.navbar {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    background-color: $navColor;
    height:60px;
    opacity: 32%;
    justify-content: space-between;

} 

.kanban-title h4 {
    color: white;
    flex: 1;
    margin: 5px;
    font-family: $lobster;
}

.navbar #logout-btn {
    opacity: 100%;
    color: white;
    border: none;
    margin: 5px;
    background-color:rgb(201, 201, 201);
    border-radius: 2px;
    width: 64px; 
   
}

.navbar #logout-btn:hover {
    cursor: pointer; 
    // opacity: 50%;
}


.content {
    height: 100%;
    display:flex;
    justify-content: flex-start;
    align-items: flex-start;
    overflow-x: auto;
   
}

.card-list, .card-list-add {
    display: flex;
    flex-direction: column; 
    background-color: $cardColor;
    margin: 5px;
    width: 240px;
    border-radius: 3px;
}

.card-list-add {
    width: 200px;
}

.card-list-add div input {
    margin: auto;
}

.card-title h3 {
    font-size: 14px;
    padding: 5px;
    margin-top: 10px;
 
}

.card-title {
    height: 40px;
}

.card-content-box {
    display: flex;
    flex-direction: column;
    min-height: auto;
    max-height: 450px;
    overflow-y: auto;  
    cursor: move; 
}

.card-content {
    background-color: white;
    margin: 4px;
    border-radius: 2px;
    padding: 5px;
    box-shadow: 1px 0px 2px 0px grey;
    font-size: 14px;
    height:auto;
    width: auto;
    
   
 
}

.card-btn button {
    height:32px;
    width: 100%;
    text-align: left;   
    background-color: $cardColor; 
    border: 0;
    padding: 5px;
    border-radius: 3px;
   
}



.card-btn button:hover {
   background-color: $cardColorHover;
   cursor: pointer;
  
}

.card-btn {
    padding: 5px;
}



